webpackJsonp([192021541813403],{

/***/ 396:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Blog by Arjun Kava","subtitle":"I am a tireless seeker of knowledge, occassional purveyor of wisdom and also, coincidentally, a deep learning engineer.","copyright":"¬© All rights reserved.","author":{"name":"Arjun Kava","twitter":"arjun_kava"},"disqusShortname":"arjun-kava","url":"https://arjun-kava.github.io/"}},"markdownRemark":{"id":"/Volumes/multimedia/Personal-Repositories/arjunkava-blog/src/pages/articles/2018-06-30--Install-Comments-Subscription-and-Analytics-Gatsby/index.md absPath of file >>> MarkdownRemark","html":"<p>After creating blog, It will be meaningless If nobody comment it with ‚ù§Ô∏è ‚ù§Ô∏è ‚ù§Ô∏è or üòç üòç üòç, So Let some emojis to be flow around. </p>\n<p>If you have not read my first blog, Please ready it first <a href=\"https://arjun-kava.github.io/posts/how-to-create-successfull-blog-in-hour/\">how-to-create-successfull-blog-in-hour</a>. Ignore if you have already read it.</p>\n<p>This blog is all about improvements on How to get user feedback through comments, How to engage with them with subscriptions and Analyze traffic.</p>\n<p>First of all Hats off to React Community and Gatsby Community. All community memebers are very active and providing variety of plugins for developers. Let‚Äôs start by adding multiple plugins integration for comments, subscriptions and trafiic analytics.</p>\n<h4><a href=\"#add-disqus-plugin-for-comments\">Add Disqus Plugin For Comments</a></h4>\n<p>We are going to use one of the plugin for integrating <a href=\"https://disqus.com\">disqus</a> with <code class=\"language-text\">gatsbyjs</code>. hit the following command from your project root.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">yarn add react-disqus-comments --save</code></pre>\n      </div>\n<p>Then create directory named <code class=\"language-text\">Disqus</code> under <code class=\"language-text\">src/components</code> directory and create <code class=\"language-text\">index.jsx</code> file also put following component code into <code class=\"language-text\">index.jsx</code> file.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">import</span> React<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> Component <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'react'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> ReactDisqusComments <span class=\"token keyword\">from</span> <span class=\"token string\">'react-disqus-comments'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">Disqus</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">constructor</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">super</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span> toasts<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>notifyAboutComment <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>notifyAboutComment<span class=\"token punctuation\">.</span><span class=\"token function\">bind</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>onSnackbarDismiss <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>onSnackbarDismiss<span class=\"token punctuation\">.</span><span class=\"token function\">bind</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">onSnackbarDismiss</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span>toasts<span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>toasts<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> toasts <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n  <span class=\"token function\">notifyAboutComment</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> toasts <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>toasts<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    toasts<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> text<span class=\"token punctuation\">:</span> <span class=\"token string\">'New comment available!!'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> toasts <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> <span class=\"token punctuation\">{</span> postNode<span class=\"token punctuation\">,</span> siteMetadata <span class=\"token punctuation\">}</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token operator\">!</span>siteMetadata<span class=\"token punctuation\">.</span>disqusShortname<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">return</span> <span class=\"token keyword\">null</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n    <span class=\"token keyword\">const</span> post <span class=\"token operator\">=</span> postNode<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">const</span> url <span class=\"token operator\">=</span> siteMetadata<span class=\"token punctuation\">.</span>url <span class=\"token operator\">+</span> postNode<span class=\"token punctuation\">.</span>fields<span class=\"token punctuation\">.</span>slug<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token operator\">&lt;</span>ReactDisqusComments\n        shortname<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>siteMetadata<span class=\"token punctuation\">.</span>disqusShortname<span class=\"token punctuation\">}</span>\n        identifier<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>title<span class=\"token punctuation\">}</span>\n        title<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>title<span class=\"token punctuation\">}</span>\n        url<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>url<span class=\"token punctuation\">}</span>\n        category_id<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">.</span>category_id<span class=\"token punctuation\">}</span>\n        onNewComment<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>notifyAboutComment<span class=\"token punctuation\">}</span>\n      <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> Disqus<span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Now open official site of <a href=\"https://disqus.com\">Disqus</a> and create your account, click on <code class=\"language-text\">Get Started</code> then it will ask two options in which select <code class=\"language-text\">I want to Install Disqus On My Site</code> as displayed in following.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/disqus-start-8e33851e98d16c629d578f439251c9d7-2e976.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 709px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 83.63892806770099%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"disqus-start.png\"\n        title=\"\"\n        src=\"/static/disqus-start-8e33851e98d16c629d578f439251c9d7-2e976.png\"\n        srcset=\"/static/disqus-start-8e33851e98d16c629d578f439251c9d7-d1776.png 240w,\n/static/disqus-start-8e33851e98d16c629d578f439251c9d7-c11a6.png 480w,\n/static/disqus-start-8e33851e98d16c629d578f439251c9d7-2e976.png 709w\"\n        sizes=\"(max-width: 709px) 100vw, 709px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em>Start With Disqus.</em></p>\n<p>After selecting above option, It will ask you to submit details for your website just add name of your site.\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/disqus-create-b9b9f6650e83a22430236ea2336dd86c-31b60.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 685px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 92.40875912408758%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"disqus-create.png\"\n        title=\"\"\n        src=\"/static/disqus-create-b9b9f6650e83a22430236ea2336dd86c-31b60.png\"\n        srcset=\"/static/disqus-create-b9b9f6650e83a22430236ea2336dd86c-bd442.png 240w,\n/static/disqus-create-b9b9f6650e83a22430236ea2336dd86c-80d87.png 480w,\n/static/disqus-create-b9b9f6650e83a22430236ea2336dd86c-31b60.png 685w\"\n        sizes=\"(max-width: 685px) 100vw, 685px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<em>Create Site With Disqus.</em></p>\n<p>So far we had setup component for Disqus, Use this component and append it at end of <code class=\"language-text\">PostTemplateDetails/index.jsx</code> file. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">src/components/PostTemplateDetails/index.jsx</code></pre>\n      </div>\n<p> below I have added three major lines which inport Disqus component, configure component and then call component in <code class=\"language-text\">render()</code> function.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token operator\">...</span>\n<span class=\"token keyword\">import</span> Disqus <span class=\"token keyword\">from</span> <span class=\"token string\">'../Disqus'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">PostTemplateDetails</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n   <span class=\"token operator\">...</span>\n   <span class=\"token keyword\">const</span> commentsBlock <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>\n     <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n       <span class=\"token operator\">&lt;</span>Disqus postNode<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>post<span class=\"token punctuation\">}</span> siteMetadata<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>data<span class=\"token punctuation\">.</span>site<span class=\"token punctuation\">.</span>siteMetadata<span class=\"token punctuation\">}</span> <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n     <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n   <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n   <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n     <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n       <span class=\"token punctuation\">{</span>homeBlock<span class=\"token punctuation\">}</span>\n       <span class=\"token operator\">&lt;</span>div className<span class=\"token operator\">=</span><span class=\"token string\">\"post-single\"</span><span class=\"token operator\">></span>\n           <span class=\"token operator\">...</span>\n           <span class=\"token punctuation\">{</span>commentsBlock<span class=\"token punctuation\">}</span>\n         <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n       <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n     <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n   <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> PostTemplateDetails<span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p><em>Code of Disqus Plugin Integration</em></p>\n<p>Next you have to add <code class=\"language-text\">short name</code> of your site under <code class=\"language-text\">siteMetadata</code> in <code class=\"language-text\">gaatsby-config.js</code> file as below.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">siteMetadata<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token operator\">...</span>\n   disqusShortname<span class=\"token punctuation\">:</span> <span class=\"token string\">'your-short-name'</span> <span class=\"token comment\">// which is website name added while creting Disqus account.</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Thats all done with Disqus, run project and now you can see comment section at the end of each post.</p>\n<h4><a href=\"#add-mailchimp-plugin-for-subscription\">Add Mail Chimp Plugin For Subscription</a></h4>\n<p>we are going to use ‚ÄòMail Chimp‚Äô for creting subscription model. Install following plugin to support Mail Chimp Integration with gatsbyjs. If you don‚Äôt have Mail Chip account open it from <a href=\"https://login.mailchimp.com/signup\">here</a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">yarn add gatsby<span class=\"token operator\">-</span>plugin<span class=\"token operator\">-</span>mailchimp <span class=\"token operator\">--</span>save</code></pre>\n      </div>\n<p>Now, you need <code class=\"language-text\">Mailchimp Endpoint</code> follow steps given at offcial plugin page <a href=\"https://www.gatsbyjs.org/packages/gatsby-plugin-mailchimp/?=mail\">gatsby-plugin-mailchimp</a>. Configure <code class=\"language-text\">Mail Chimp Plugin</code> be adding following line into <code class=\"language-text\">gatsby-config.js</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token operator\">...</span>\n  <span class=\"token punctuation\">{</span>\n    resolve<span class=\"token punctuation\">:</span> <span class=\"token string\">'gatsby-plugin-mailchimp'</span><span class=\"token punctuation\">,</span>\n    options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n      endpoint<span class=\"token punctuation\">:</span> <span class=\"token string\">'your mail chip end point url'</span><span class=\"token punctuation\">,</span> <span class=\"token comment\">// see instructions at official plugin page</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span></code></pre>\n      </div>\n<p>I have created small component to call Mail Chimp API for adding user into list of subscription as below. Add this code into <code class=\"language-text\">Subscribe/index.jsx</code> by creating new component under <code class=\"language-text\">src/components</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token keyword\">import</span> React<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> Component <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'react'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> addToMailchimp <span class=\"token keyword\">from</span> <span class=\"token string\">'gatsby-plugin-mailchimp'</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">Subscribe</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token function\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">super</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n            email<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">``</span></span><span class=\"token punctuation\">,</span>\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\">// Update state each time user edits their email address</span>\n    <span class=\"token function-variable function\">_handleEmailChange</span> <span class=\"token operator\">=</span> e <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n        <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> email<span class=\"token punctuation\">:</span> e<span class=\"token punctuation\">.</span>target<span class=\"token punctuation\">.</span>value <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token comment\">// Post to MC server &amp; handle its response</span>\n    <span class=\"token function-variable function\">_postEmailToMailchimp</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>email<span class=\"token punctuation\">,</span> attributes<span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n        <span class=\"token function\">addToMailchimp</span><span class=\"token punctuation\">(</span>email<span class=\"token punctuation\">,</span> attributes<span class=\"token punctuation\">)</span>\n            <span class=\"token punctuation\">.</span><span class=\"token function\">then</span><span class=\"token punctuation\">(</span>result <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n                <span class=\"token comment\">// Mailchimp always returns a 200 response</span>\n                <span class=\"token comment\">// So we check the result for MC errors &amp; failures</span>\n                <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>result<span class=\"token punctuation\">.</span>result <span class=\"token operator\">!==</span> <span class=\"token template-string\"><span class=\"token string\">`success`</span></span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n                    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n                        status<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`error`</span></span><span class=\"token punctuation\">,</span>\n                        msg<span class=\"token punctuation\">:</span> result<span class=\"token punctuation\">.</span>msg<span class=\"token punctuation\">,</span>\n                    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n                <span class=\"token punctuation\">}</span> <span class=\"token keyword\">else</span> <span class=\"token punctuation\">{</span>\n                    <span class=\"token comment\">// Email address succesfully subcribed to Mailchimp</span>\n                    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n                        status<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`success`</span></span><span class=\"token punctuation\">,</span>\n                        msg<span class=\"token punctuation\">:</span> result<span class=\"token punctuation\">.</span>msg<span class=\"token punctuation\">,</span>\n                    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n                <span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n            <span class=\"token punctuation\">.</span><span class=\"token keyword\">catch</span><span class=\"token punctuation\">(</span>err <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n                <span class=\"token comment\">// Network failures, timeouts, etc</span>\n                <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n                    status<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`error`</span></span><span class=\"token punctuation\">,</span>\n                    msg<span class=\"token punctuation\">:</span> err<span class=\"token punctuation\">,</span>\n                <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n\n    <span class=\"token function-variable function\">_handleFormSubmit</span> <span class=\"token operator\">=</span> e <span class=\"token operator\">=></span> <span class=\"token punctuation\">{</span>\n        e<span class=\"token punctuation\">.</span><span class=\"token function\">preventDefault</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n        e<span class=\"token punctuation\">.</span><span class=\"token function\">stopPropagation</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n\n        <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token operator\">!</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>email<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n            <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n                status<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`error`</span></span><span class=\"token punctuation\">,</span>\n                msg<span class=\"token punctuation\">:</span> <span class=\"token string\">\"Please enter valid email!\"</span><span class=\"token punctuation\">,</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n        <span class=\"token punctuation\">}</span>\n        <span class=\"token keyword\">else</span> <span class=\"token punctuation\">{</span>\n\n            <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">setState</span><span class=\"token punctuation\">(</span>\n                <span class=\"token punctuation\">{</span>\n                    status<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`sending`</span></span><span class=\"token punctuation\">,</span>\n                    msg<span class=\"token punctuation\">:</span> <span class=\"token keyword\">null</span><span class=\"token punctuation\">,</span>\n                <span class=\"token punctuation\">}</span>\n\n            <span class=\"token punctuation\">)</span>\n            <span class=\"token comment\">// setState callback (subscribe email to MC)</span>\n            <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">_postEmailToMailchimp</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>email<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n                pathname<span class=\"token punctuation\">:</span> document<span class=\"token punctuation\">.</span>location<span class=\"token punctuation\">.</span>pathname<span class=\"token punctuation\">,</span>\n            <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n        <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n\n\n    <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n\n        <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n            <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n                <span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>status <span class=\"token operator\">===</span> <span class=\"token template-string\"><span class=\"token string\">`success`</span></span> <span class=\"token operator\">?</span> <span class=\"token punctuation\">(</span>\n                    <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>Thank you<span class=\"token operator\">!</span> You ºll receive your first email shortly<span class=\"token punctuation\">.</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n                <span class=\"token punctuation\">)</span> <span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span>\n                        <span class=\"token operator\">&lt;</span>div <span class=\"token operator\">></span>\n                            <span class=\"token operator\">&lt;</span>span<span class=\"token operator\">></span>Enjoyed <span class=\"token keyword\">this</span> post<span class=\"token operator\">?</span> Want to Receive the next one <span class=\"token keyword\">in</span> your inbox<span class=\"token operator\">!</span><span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>span<span class=\"token operator\">></span>\n                            <span class=\"token operator\">&lt;</span>form\n                                id<span class=\"token operator\">=</span><span class=\"token string\">\"email-capture\"</span>\n                                method<span class=\"token operator\">=</span><span class=\"token string\">\"post\"</span>\n                                noValidate\n                            <span class=\"token operator\">></span>\n                                <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n                                    <span class=\"token operator\">&lt;</span>input\n                                        placeholder<span class=\"token operator\">=</span><span class=\"token string\">\"you@email.com\"</span>\n                                        onChange<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>_handleEmailChange<span class=\"token punctuation\">}</span>\n                                        required\n                                    <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n                                    <span class=\"token operator\">&lt;</span>button\n                                        className<span class=\"token operator\">=</span><span class=\"token string\">\"icon-mail\"</span>\n                                        type<span class=\"token operator\">=</span><span class=\"token string\">\"submit\"</span>\n                                        onClick<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>_handleFormSubmit<span class=\"token punctuation\">}</span>\n                                    <span class=\"token operator\">></span>\n\n                                    <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>button<span class=\"token operator\">></span>\n                                    <span class=\"token punctuation\">{</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>status <span class=\"token operator\">===</span> <span class=\"token template-string\"><span class=\"token string\">`error`</span></span> <span class=\"token operator\">&amp;&amp;</span> <span class=\"token punctuation\">(</span>\n                                        <span class=\"token operator\">&lt;</span>div\n                                            dangerouslySetInnerHTML<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> __html<span class=\"token punctuation\">:</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>state<span class=\"token punctuation\">.</span>msg <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span>\n                                        <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n                                    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span>\n                                <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n                            <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>form<span class=\"token operator\">></span>\n                        <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n                    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span>\n            <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n        <span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>The above configuration is for creating and integrating <code class=\"language-text\">Subscribe Plugin</code> while we need to call this component to display subscription form. For this purpose we are going to use <code class=\"language-text\">src/PostTemplateDetails/index.jsx</code> component add subscribe block above <code class=\"language-text\">{CommentBlock}</code> added earlier in this post as below.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\"><span class=\"token operator\">...</span>\n<span class=\"token keyword\">import</span> Subscribe <span class=\"token keyword\">from</span> <span class=\"token string\">'../Subscribe'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">PostTemplateDetails</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token operator\">...</span>\n    <span class=\"token keyword\">const</span> SubscribeBlock <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n        <span class=\"token operator\">&lt;</span>Subscribe <span class=\"token operator\">/</span><span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token operator\">&lt;</span>div<span class=\"token operator\">></span>\n        <span class=\"token punctuation\">{</span>homeBlock<span class=\"token punctuation\">}</span>\n        <span class=\"token operator\">&lt;</span>div className<span class=\"token operator\">=</span><span class=\"token string\">\"post-single\"</span><span class=\"token operator\">></span>\n            <span class=\"token operator\">...</span>\n            <span class=\"token punctuation\">{</span>SubscribeBlock<span class=\"token punctuation\">}</span>\n            <span class=\"token punctuation\">{</span>CommentBlock<span class=\"token punctuation\">}</span>\n          <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n        <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n      <span class=\"token operator\">&lt;</span><span class=\"token operator\">/</span>div<span class=\"token operator\">></span>\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> PostTemplateDetails<span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Run the project and Now You can see subscription block at end of each post and Test it by adding new Email into it. Which will be reflect to your <code class=\"language-text\">Mail Chimp List</code> created earlier. Amazing!</p>\n<h4><a href=\"#add-mailchimp-plugin-for-subscription\">Add Google Analytics Plugin For Tracking Trafic</a></h4>\n<p>So far we had added Feedback to our blog but It is more important to see How many users have reached to blog and from where traffic flows. I am going to introduce third plugin by <code class=\"language-text\">gatsbyjs</code> community called <a href=\"https://www.gatsbyjs.org/packages/gatsby-plugin-google-analytics/?=gatsby-plugin-google-analytics\">gatsby-plugin-google-analytics</a>. You should have to sign up through <a href=\"https://www.google.com/analytics/#?modal_active=none\">Google Analytics</a> and get tracking id from Analytics by creating New Website Account. Easy!</p>\n<p>Install plugin into project by executing fllowing command.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">yarn add gatsby-plugin-google-analytics --save</code></pre>\n      </div>\n<p>and then add following configuration to your <code class=\"language-text\">gatsby-config.js</code> file. Here you have to replace <code class=\"language-text\">UA-YOUR-TRACKING-ID</code> to tracking id of your google analytics account.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code class=\"language-javascript\">plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token operator\">...</span>\n    <span class=\"token punctuation\">{</span>\n      resolve<span class=\"token punctuation\">:</span> <span class=\"token string\">'gatsby-plugin-google-analytics'</span><span class=\"token punctuation\">,</span>\n      options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> trackingId<span class=\"token punctuation\">:</span> <span class=\"token string\">'UA-YOUR-TRACKING-ID'</span> <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">]</span></code></pre>\n      </div>\n<p>Today, we had done so many things to make our blog more robust, dynamic and also user friendly. After completion of these steps, We are now ready to hit deployment So why we are wasting more time? just hit following command and see these changes in your live blog.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">run npm deploy</code></pre>\n      </div>\n<p>P.S. You can get reference of all these code on my blog repository <a href=\"https://github.com/arjun-kava/arjun-kava.github.io\">arjun-kava.github.io</a> under <code class=\"language-text\">gatsby</code> branch. If you like post and want next in your inbox hit subscription button with your email. Also give your opinion about post in below comment box. Happy Exploring.</p>\n<p>As a deep learning engineer from today onwards I am going to write more about my learning journey of Computer Vision, Deep Learning and related libraries. Stay In Touch.</p>","fields":{"tagSlugs":["/tags/reactjs/","/tags/gatsbyjs/","/tags/javascript/","/tags/disqus/","/tags/mailchimp/","/tags/google-analytics/"]},"frontmatter":{"title":"Improve Blogging With Feedback and Analytics","tags":["reactjs","gatsbyjs","javascript","disqus","mailchimp","google analytics"],"date":"2018-07-03T16:04:34.581Z","description":"After creating blog, It will be meaningless If nobody comment it with ‚ù§Ô∏è ‚ù§Ô∏è ‚ù§Ô∏è or üòç üòç üòç , So Let some emojis to be flow around."}}},"pathContext":{"slug":"/posts/improve-blogging-with-feedback-and-analytics/"}}

/***/ })

});


